import React from 'react'
import styles from '../styles/UploadProgress.module.css'
import { RiCheckboxCircleFill } from 'react-icons/ri'

const UploadProgress = ({uploadProgress,filename}) => {
    return (
        <div className={styles.progress__container}>
            <p>{filename} {(uploadProgress === 100) ? <RiCheckboxCircleFill /> : null}</p>
            {uploadProgress < 100 ? <div className={styles.progress__bar} ><span style={{ width: `${uploadProgress}%` }}></span></div> : null}
        </div>
    )
}

export default UploadProgress