import React, { useState, useEffect } from 'react'
import axios from 'axios';
import { useParams } from 'react-router-dom';
import ScaleLoader  from 'react-spinners/ScaleLoader'
import Download from '../components/Download';
import styles from '../styles/Download.module.css';


const DownloadPage = () => {
    const [loading, setLoading] = useState(true);
    const [hasError, setHasError] = useState(true);

    const [file, setfile] = useState({
        documentName: null,
        size: null,
        createdDate: null,
        expiryDate: null,
        downloadLink: null,
    });

    const apiUrl = "http://localhost:3001/api/documents";
    const { id } = useParams();
    const formatBytes = (a, b = 2, k = 1024) => {
        const d = Math.floor(Math.log(a) / Math.log(k));
        return a === 0
            ? '0 Bytes'
            : `${parseFloat((a / k ** d).toFixed(Math.max(0, b)))} ${
                  ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'][d]
              }`;
    };

    useEffect(() => {
        axios
            .get(`${apiUrl}/${id}`)
            .then((res) => {
                const uploadDate = new Date(res.data.createdDate).toDateString();
                const expDate = new Date(
                    +new Date(res.data.createdDate) + 24 * 60 * 60 * 1000
                ).toDateString();
                const response = {
                    documentName: res.data.documentName,
                    size: formatBytes(res.data.size),
                    createdDate: uploadDate,
                    expiryDate: expDate,
                    downloadLink: res.data.downloadLink,
                };
                setHasError(false);
                setfile(response);
                
                
                setLoading(false);
            })
            .catch((err) => console.log(err));
    }, []);




    return ( 
        <>
            { loading ? <div className={styles.spinner__container}><ScaleLoader loading={loading}/></div> : <Download id={id} file={file}/>}
        </>
    );
}

export default DownloadPage
