import React from "react";
import Button from "../components/Button";
import FileDetails from "../components/FileDetails";
import download from "../assets/images/download.svg";
import error from "../assets/images/error.svg";
import { HiDownload } from "react-icons/hi";
import styles from "../styles/Download.module.css";

const Download = ({ id, file, hasError }) => {
  const apiUrl = `${REACT_APP_BASE_URL}/api/documents`;

  const downloadHandler = (event) => {
    event.preventDefault();
    window.location.href = `${apiUrl}/download/${id}`;
  };

  return (
    <section className={styles.download__container}>
      <img src={!hasError ? download : error} alt="Download" />
      <h1>
        {!hasError
          ? "Your Download is Ready"
          : "Whoops! File has expired or deleted "}
      </h1>
      <FileDetails file={file} hasError={hasError} />
      <Button
        className={styles.download__btn}
        text="Download"
        iconRight={<HiDownload />}
        onClick={downloadHandler}
      />
    </section>
  );
};

export default Download;
