import React, { useState } from 'react';
import styles from '../styles/Textbox.module.css'

const Textbox = ({type ,placeholder="", extraStyles, name, onValueChange}) => {
    const [textValue, setTextValue] = useState('');  

    const handleChange = (e) => {
        const name = e.target.name;
        const value = e.target.value;
        onValueChange(name, value);
        setTextValue(value);
    }

    return (
        <input required className={styles.textbox} name={name} type={type} value={textValue} onChange={handleChange} placeholder={placeholder} style={extraStyles}/>
        
    )
}

export default Textbox
