import React, { useState, useRef } from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";
import styles from "../styles/ShareComponent.module.css";
import Button from "./Button";
import Textbox from "./Textbox";
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";

const ShareComponent = ({ downloadLink, uuid }) => {
  const downloadLinkBox = useRef(null);
  const [showForm, setShowForm] = useState(false);
  const [sender, setSender] = useState("");
  const [receiver, setReceiver] = useState("");

  const changeValue = (target, value) => {
    target === "sender" ? setSender(value) : setReceiver(value);
  };

  const handleCopy = () => {
    downloadLinkBox.current.select();
    toast.success("Link Copied To Clipboard");
  };

  const sendMail = (event) => {
    event.preventDefault();
    if (uuid) {
      if (sender !== receiver) {
        const data = { sender, receiver, uuid };
        const apiUrl = `${REACT_APP_BASE_URL}/api/documents/send`;
        axios
          .post(apiUrl, data)
          .then((response) => {
            toast.success("Successfully shared via email");
          })
          .catch((error) => {
            toast.error("An error occured sending the email!");
          });
      } else {
        toast.error("Sender and receiver must be different");
      }
    } else {
      toast.info("Please select a file to upload!");
    }
  };

  return (
    <div className={styles.share__container}>
      <input
        type="text"
        readOnly={true}
        value={downloadLink}
        onClick={handleCopy}
        ref={downloadLinkBox}
      />
      <div className={styles.link__wrapper}>
        <CopyToClipboard text={downloadLink} onCopy={handleCopy}>
          <Button text="Copy to clipboard" />
        </CopyToClipboard>
        <Button text="Share Via Email" onClick={() => setShowForm(!showForm)} />
      </div>

      {showForm ? (
        <form action="" className={styles.textbox__wrapper}>
          <Textbox
            type="email"
            name="sender"
            placeholder="Sender's Email"
            onValueChange={changeValue}
          />
          <Textbox
            type="email"
            name="receiver"
            placeholder="Receiver's Email"
            onValueChange={changeValue}
          />
          <Button text="send" type="submit" onClick={sendMail} />
        </form>
      ) : null}

      <ToastContainer position="bottom-center" theme="colored" />
    </div>
  );
};

export default ShareComponent;
